<template>
  <div class="trip bg-primary">
    <div class="banner mb-5">
      <img width="100%" src="../assets/images/bg_trip.png" alt="">
    </div>
    <div class="content px-6 d-flex">
      <div class="content-box">
        <div class="nav d-flex bg-white d-flex justify-content-center">
          <div class="score p-5 w-50 border-right" @click="rate = !rate, change({sort:'rate'})">評分排序</div>
          <div class="price p-5 w-50" @click="price = !price, change({sort:'price'})">價格排序</div>
        </div>
        <div class="trip-detail-list py-2">
          <div class="trip-detail bg-white rounded mb-2 p-4 text-left position-relative" v-for="(item, index) in triplist" :key="index">
            <img :src="item.image_url" class="mr-5" height="200px" width="250px" alt="">
            <div class="position-relative w-100">
              <div>
              <p class="h6 text-gray">{{ item.agency }}</p>
              <a :href="item.tour_detail_url"><p class="h5 text-secondary">{{ item.title}}</p></a>
              <div class="d-flex mb-2">
                <div class="star-light rounded-circle mr-1" v-for="star in item.rating" :key="star"></div>
                <div v-if="item.rating !== 5" class="d-flex ">
                  <div class="star-dark rounded-circle mr-1" v-for="dark in 5-item.rating" :key="dark"></div>
                </div>
              </div>
            </div>
            <div class="trip-detail-content w-100 d-flex justify-content-between">
              <div class="w-75">
                <div class="d-flex flex-wrap mb-3">
                  <p class="rounded bg-secondary text-nowrap py-1 px-2 mr-2 mb-2 text-white" v-for="(tag,index) in item.tags" :key="index">{{ tag }}</p>
                </div>
                <div class="d-flex flex-wrap">
                  <div class="rounded border border-secondary text-nowrap p-1 mr-2 mb-2 text-secondary" v-for="(groupdetail,index) in item.group" :key="index">
                    <p class="mb-0 h6 d-flex justify-content-center">{{ groupdetail.date | dateformate }}</p>
                    <p class="mb-0 h7 d-flex justify-content-center">可售{{ groupdetail.quantity }}位</p>
                  </div>
                </div>
              </div>
              <div class="text-nowrap float-right d-flex align-items-end">
                <p class="text-danger h4 mb-0">{{ item.tour_days }} </p> 天<p class="text-danger h4 mb-0">${{ item.min_price }} </p> 起
              </div>
            </div>
            </div>
          </div>
        </div>
        <div class="page d-flex float-right text-secondary h6">
          <p v-show="page > 1" @click="getTrip(page--)"><u>上一頁</u></p>
          <p class="mx-2">{{ page }}</p>
          <p v-show="lastpage" @click="getTrip(page++)"><u>下一頁</u></p>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import axios from 'axios'
export default {
  name: 'Trip',
  data () {
    return {
      triplist: [],
      sortchange: '',
      rate: false, // rating_desc
      price: false, // price_desc
      page: 1,
      lastpage: true
    }
  },
  created () {
    this.getTrip()
  },
  filters: {
    dateformate (date) {
      return date.substring(5).replace('-', '/')
    }
  },
  methods: {
    getTrip () {
      axios.get('https://interview.tripresso.com/tour/search', {
        params: {
          page: this.page,
          row_per_page: 10,
          sort: this.sortchange
        }
      }).then(res => {
        console.log(res)
        if (res.data.data.tour_list.length > 0) {
          this.triplist = res.data.data.tour_list
          this.lastpage = true
        } else {
          this.page--
          this.lastpage = false
        }
      })
    },
    change (item) {
      if (item.sort === 'rate') {
        this.sortchange = this.rate ? 'rating_desc' : 'rating_asc'
      } else {
        this.sortchange = this.price ? 'price_desc' : 'price_asc'
      }
      this.getTrip()
    }
  }
}
</script>
